/* pace  */

.close span {
    cursor: pointer;
}

.shadow {
    -webkit-box-shadow: 0 0.125rem 0.8rem rgba(0, 0, 0, 0.1) !important;
    box-shadow: 0 0.125rem 0.8rem rgba(0, 0, 0, 0.1) !important;
}

.text-uppercase {
    letter-spacing: 0.2em;
}

.text-lg {
    font-size: 1.4rem !important;
}

.text-base {
    font-size: 0.9rem !important;
}

.text-sm {
    font-size: 0.7875rem !important;
}

.roundy {
    border-radius: 50px;
}

.border {
    border: 1px solid #eee;
}

.rounded {
    border-radius: 0.5rem !important;
}

.push {
    margin-left: 6rem;
}

.headings-font-family {
    /* font-family: "Poppins", sans-serif !important; */
}

.text-gray {
    color: #adb5bd;
}

.dot {
    max-width: 10px;
    min-width: 10px;
    max-height: 10px;
    min-height: 10px;
    border-radius: 50%;
}

.dot-sm {
    max-width: 0.5rem;
    min-width: 0.5rem;
    max-height: 0.5rem;
    min-height: 0.5rem;
    border-radius: 50%;
}

a {
    -webkit-transition: all 0.3s;
    transition: all 0.3s;
}

.page-holder {
    overflow-x: hidden;
    min-height: calc(100vh - 72px);
}

.icon {
    min-width: 2.2rem;
    max-width: 2.2rem;
    min-height: 2.2rem;
    max-height: 2.2rem;
    border-radius: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}

.icon-lg {
    min-width: 3rem;
    max-width: 3rem;
    min-height: 3rem;
    max-height: 3rem;
}

.icon-sm {
    min-width: 1.7rem;
    max-width: 1.7rem;
    min-height: 1.7rem;
    max-height: 1.7rem;
    font-size: 0.7rem;
}

button {
    cursor: pointer;
    outline: none !important;
}

.smaller {
    font-size: 0.7rem;
}

.no-anchor-style {
    color: inherit !important;
    text-decoration: none !important;
}

.line {
    height: 1px;
    border-bottom: 1px dashed #eee;
    margin: 2rem 0;
}
